.rebate-box {
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 1px solid #203b60;

  .rebate-box-info {
    width: 1145px;
    margin-top: 112px;

    .rebate-box-header {
      width: 100%;
      height: 200px;
      display: flex;
      align-items: center;
      padding: 0 36px;
      border: 1px solid #203B60;
      background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);

      .rebate-account-reward-info {
        font-weight: 500;
        font-size: 20px;
        color: #93A1B8;
        margin-left: 36px;

        .rebate-reward {
          margin-bottom: 36px;
          display: flex;
          align-items: center;

          .reward-btn {
            display: flex;
            width: 100px;
            height: 32px;
            align-items: center;
            justify-content: flex-end;
            padding: 0 8px;
            background: #5577FD;
            border-radius: 80.6452px;
            margin: 0 8px;
            color: #E0ECFF;

            img {

              margin-left: 12px;
            }
          }
        }
      }
    }

    .rebate-box-use-info {
      width: 100%;
      height: 320px;
      border: 1px solid #203B60;
      margin-top: 25px;
      padding: 52px 46px 70px 46px;

      .rebate-box-user-info-header {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .rebate-box-user-info-title {
          font-weight: 500;
          font-size: 24px;
          color: #E0ECFF;

          span {
            font-size: 14px;
            color: #93A1B8;
          }
        }

        .wallet-account-box {
          width: 192px;
          height: 48px;
          border: 1px solid #203B60;
          border-radius: 4px;
          display: flex;
          align-items: center;
          justify-content: center;
          font-weight: 700;
          font-size: 16px;
          color: #E0ECFF;

          img {
            margin-right: 12px;
          }
        }
      }

      .rebate-box-use-info-box {
        width: 1052px;
        height: 96px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 74px;

        .total-users-bg,
        .total-fee-bg,
        .total-rewards-bg {
          width: 336px;
          height: 96px;
          border-radius: 4px;
          display: flex;
          align-items: center;
          justify-content: center;
          background:linear-gradient(255.96deg, #CD7A37 0%, #E0ECFF 49.48%, #5577FD 100%);
          .total-users-box,.total-fee-box,.total-rewards-box{
            width: 334px;
            height: 94px;
            border-radius: 4px;
            background-color:  #0A172B;
            background-image: url('./img/total-users.png');
            background-size: 100% 100%;
            display: flex;
            align-items: center;
            padding-left: 24px;
            img{
              margin-right: 14px;
            }
            .total-users-box-info,.total-fee-box-info,.total-rewards-box-info{
              font-size: 24px;
              color: #E0ECFF;
              .total-users-box-info-title,.total-fee-box-info-title,.total-rewards-box-info-title{
                font-size: 20px;
                color: #93A1B8;
              }
            }   
          }
          .total-rewards-box{
            background-image: url('./img/total-fee.png');
          }
          .total-fee-box{
            background-image: url('./img/total-rewards.png');
          }
        }
      }
    }
  }
}